import React, {Component} from 'react';
import axios from 'axios';
import Mustache from 'mustache';

import Item from './Item';
import './styles.scss';


class WordLiftNavigator extends Component{
  constructor(props){
    super(props);
    this.state = {
      isLoading: true,
      isError: false,
      data: null,
      error: null
    }
  }

  getTemplate() {
      return this.props.templateId && this.props.templateId.trim() !== '' && document.getElementById(this.props.templateId) && document.getElementById(this.props.templateId).innerText.trim();
  }

  renderContent() {
      if(!this.getTemplate()){
          return <div className="wordlift-navigator"><h2 className="title">{this.props.title}</h2><section className="cards">{this.state.data.map(item => <Item key={item.post.permalink} {...item} />)}</section></div>;
      } else {
          return <div className={`${this.props.templateId}-wrapper`} dangerouslySetInnerHTML={{__html: Mustache.render(this.getTemplate(), {
              items: this.state.data,
              title: this.props.title
          })}} />;
      }
  }

  componentDidMount() {
    axios.get(this.props.restUrl)
        .then( response => {
          this.setState({
            isLoading: false,
            isError: false,
            data: response.data && response.data.slice(0, this.props.limit)
          })
        })
        .catch(error => {
          this.setState({
            isLoading: false,
            isError: true,
            error
          })
        });
  }

  render(){
    return (
        (this.state.isLoading && <div>Loading...</div>) ||
        (!this.state.isLoading && (!this.state.data || this.state.isError) && <div>Error or No Data</div>) ||
        (!this.state.isLoading && this.state.data && this.renderContent())
    )
  }
}

export default WordLiftNavigator;
