import tippy from 'tippy.js';
import axios from 'axios';
import axiosWrapper from 'axios-cache-plugin';
import './wordlift-theme.css'

const contentParser = (json) => {
    let content = json[0],
        description = content.description;
    if(!content.image) {
        return description;
    }
    let image = content.image[0];
    if(image.width >= image.height){
        return `<img src="${image.url}" style="width: 100%; height: 200px; object-fit: cover;" />${description}`;
    }
    return `<img src="${image.url}" style="width: 50%; height: auto; float: right" />${description}<div style="clear: right"></div>`
};

const axiosCache = axiosWrapper(axios, {
    maxCacheSize: 15,
    excludeHeaders: true
});
axiosCache.__addFilter(/./);

export default (selector, baseUrl, tippyOptions = {}) => {

    let triggers = document.querySelectorAll(selector);

    let tippyDefaultOptions = {
        animateFill: false,
        arrow: true,
        interactive: true,
        distance: 5,
        animation: 'fade',
        flipOnUpdate: true,
        theme: 'wordlift'
    };

    triggers.forEach(trigger => {
        trigger.removeAttribute('title');
        trigger.addEventListener("mouseover", function( event ) {
            axiosCache
                .get(`${baseUrl}?entity_url=${encodeURIComponent(trigger.getAttribute('href'))}`)
                .then(response => {
                    let parsedContent = response.data && contentParser(response.data);
                    if(parsedContent.trim() !== '') {
                        let tippyInstance = tippy(trigger, {...Object.assign(tippyDefaultOptions, tippyOptions, {content: parsedContent})});
                        tippyInstance && tippyInstance.show();
                    }
                })

        }, false);
    })

}

